.wrap {
    margin-top: 80px;
}

.navbar-logo {
    background-image: url('/images/logo/logo_min@2x.png');
    background-repeat: no-repeat;
    background-position: top left;
    padding-left: 50px;
    background-size: 50px;
}

.question-in-faq {
    position: relative;
}


.question-in-faq:hover .remove {
    display: block;
}

.question-in-faq .remove {
    position: absolute;
    top: 20px;
    right: 20px;
    display: none;
}

.question-in-faq-file {
    margin-top: 10px;
    margin-bottom: 10px;
}

.portfolio-gallery {
    border: 2px solid red;
}